import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'
import { AppBar, Toolbar, IconButton, Drawer, List, ListItem, ListItemText, Typography } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
        textAlign: 'left'
    },
    emoji: {
        marginLeft: theme.spacing(1)
    },
    drawerList: {
        width: '250px'
    }
}));

const lights = [
    { title: 'Luz 1', id: 1 },
    { title: 'Luz 2', id: 2 },
    { title: 'Luz 3', id: 3 },
    { title: 'Luz 4', id: 4 },
    { title: 'Luz 5', id: 5 },
    { title: 'Luz 6', id: 6 },
    { title: 'Luz 7', id: 7 },
    { title: 'Luz 8', id: 8 },
    { title: 'Luz 9', id: 9 },
    { title: 'Luz 10', id: 10 }
];

function Navigation(props) {
    const classes = useStyles();
    let history = useHistory();
    const [drawerOpened, setDrawerOpened] = useState(false);
    const [currentLight, setCurrentLight] = useState(undefined);

    const handleDrawerOpen = () => {
        setDrawerOpened(true);
    }

    const handleDrawerClose = () => setDrawerOpened(false);

    const handleListItemClick = id => () => {
        setDrawerOpened(false);
        setCurrentLight(id);

        if (currentLight !== id)
            history.push(`/light/${id}`);
    }

    return (
        <>
            <AppBar position="sticky">
                <Toolbar>
                    <IconButton
                        edge="start"
                        color="inherit"
                        onClick={handleDrawerOpen}
                        className={classes.menuButton}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>
                        Luces de navidad
                        <span
                            role="img"
                            aria-label="árbol de navidad"
                            className={classes.emoji}
                        >
                            🎄
                        </span>
                    </Typography>
                </Toolbar>
            </AppBar>
            <Drawer
                open={drawerOpened}
                onClose={handleDrawerClose}
            >
                <List className={classes.drawerList}>
                    {lights.map(x => (
                        <ListItem button key={x.title} onClick={handleListItemClick(x.id)}>
                            <ListItemText primary={x.title} />
                        </ListItem>
                    ))}
                </List>
            </Drawer>
        </>
    )
}

export default Navigation

