export const lightModes = {
    drop: 'drop',
    rainbow: 'rainbow',
    custom: 'custom'
}

export async function getLightSettings(light) {
    // const response = await fetch(`/get/${light}`);
    // const settings = await response.json();
    const settings = JSON.parse(localStorage.getItem(light));
    if (!settings) {
        createFirstSettings();
        return getLightSettings(light);
    }
    return settings;
}

export async function updateLightSettings(light, mode, params) {
    // fetch(`/update/${light}`, {
    //     method: 'POST',
    //     body: JSON.stringify({ mode, params })
    // });
    let settings = await getLightSettings(light);
    settings.mode = mode;
    settings.params = params;
    localStorage.setItem(light, JSON.stringify(settings));
}

