import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { makeStyles, TextField, InputAdornment, Grid } from '@material-ui/core'

const useStyles = makeStyles(theme => ({
    container: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between'
    },
    square: {
        width: '80px',
        height: '40px',
        border: `3px solid ${theme.palette.grey.A100}`
    },
    textField: {
        width: '100px',
        textTransform: 'uppercase'
    }
}));

const isValidColor = color => /[0-9a-f]{6}/i.test(color);

function LedConfig(props) {
    const classes = useStyles();
    const [state, setState] = useState({
        value: props.initialValue,
        error: false
    });

    useEffect(() => {
        setState(prevState => ({ ...prevState, value: props.initialValue }))
    }, [props.initialValue]);

    const handleChange = event => {
        const value = event.target.value;
        setState({ ...state, value: value });

        if (isValidColor(value) && props.onChange)
            props.onChange(value);
    }

    const handleBlur = event => {
        setState({
            ...state,
            error: !isValidColor(event.target.value)
        });
    }

    return (
        <Grid
            container
            spacing={2}
            alignItems="center"
        >
            <Grid item>
                <div className={classes.square} style={{ backgroundColor: `#${state.value}` }}>
                </div>
            </Grid>
            <Grid item>
                <TextField
                    className={classes.textField}
                    value={state.value}
                    error={state.error}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    autoComplete="off"
                    margin="dense"
                    inputProps={{
                        maxLength: 6,
                        style: { textTransform: 'uppercase' }
                    }}
                    InputProps={{
                        startAdornment: <InputAdornment position="start">#</InputAdornment>
                    }}
                />
            </Grid>
        </Grid>
    )
}

LedConfig.propTypes = {
    initialValue: PropTypes.string,
    onChange: PropTypes.func
}

export default LedConfig
