import React, { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { Container, makeStyles, Select, MenuItem, InputLabel, FormControl } from '@material-ui/core';
import { getLightSettings, lightModes, updateLightSettings } from '../utils/settings';
import DropMode from './modes/DropMode';
import CustomMode from './modes/CustomMode';

const useStyles = makeStyles(theme => ({
    container: {
        marginTop: theme.spacing(3)
    },
    modeSettings: {
        marginTop: theme.spacing(2)
    }
}))

function LightConfig(props) {
    let { lightId } = useParams();
    const classes = useStyles();
    const label = React.useRef(null);
    const [settings, setSettings] = useState({});
    const [labelWidth, setLabelWidth] = useState(0);

    useEffect(() => {
        setLabelWidth(label.current.offsetWidth);
    }, []);

    useEffect(() => {
        async function fetchSettings() {
            const fetchedSettings = await getLightSettings(lightId);
            setSettings(fetchedSettings);
        }
        fetchSettings();
    }, [lightId]);

    const handleModeChange = event => {
        const mode = event.target.value;
        setSettings({ ...settings, mode: mode });
        updateLightSettings(lightId, mode, settings.params);
    }

    const handleChange = (params, index) => {
        let newParams;
        if (Array.isArray(params)) {
            newParams = params;
        }
        else {
            if (index >= 0 && index < 8) {
                newParams = settings.params;
                newParams[index] = params;
            }
            else {
                return;
            }
        }

        setSettings({ ...settings, params: newParams });
        updateLightSettings(lightId, settings.mode, newParams);
    }

    return (
        <Container className={classes.container}>
            <FormControl
                fullWidth
                margin="dense"
                variant="outlined"
            >
                <InputLabel shrink id="mode" ref={label}>Modo</InputLabel>
                <Select
                    labelId="mode"
                    value={settings.mode || lightModes.drop}
                    onChange={handleModeChange}
                    labelWidth={labelWidth}
                >
                    <MenuItem value={lightModes.drop}>Gota</MenuItem>
                    <MenuItem value={lightModes.rainbow}>Arcoiris</MenuItem>
                    <MenuItem value={lightModes.custom}>Personalizado</MenuItem>
                </Select>
            </FormControl>
            <div className={classes.modeSettings}>
                {settings.mode === lightModes.drop &&
                    <DropMode onChange={handleChange} initialValue={settings.params[0]} />
                }
                {settings.mode === lightModes.custom &&
                    <CustomMode onChange={handleChange} initialValues={settings.params} />
                }
            </div>
        </Container>
    )
}

export default LightConfig
