import React from 'react';
import './App.css';
import { HashRouter, Switch, Route } from 'react-router-dom';
import LightConfig from './components/LightConfig';
import { CssBaseline, ThemeProvider, createMuiTheme, useMediaQuery } from '@material-ui/core';
import Navigation from './components/Navigation';
import { red, green } from '@material-ui/core/colors';

function App() {
    const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');

    const theme = React.useMemo(
        () => {
            let primary, secondary;
            if (prefersDarkMode) {
                primary = { main: red[700] };
                secondary = { main: green.A700 }
            }
            else {
                primary = red;
                secondary = { main: green.A700 };
            }
            return createMuiTheme({
                palette: {
                    type: prefersDarkMode ? 'dark' : 'light',
                    primary: primary,
                    secondary: secondary
                }
            });
        },
        [prefersDarkMode]
    );

    return (
        <ThemeProvider theme={theme}>
            <div className="App">
                <CssBaseline />
                <HashRouter>
                    <Navigation />
                    <Switch>
                        <Route exact path="/light/:lightId" component={LightConfig} />
                    </Switch>
                </HashRouter>
            </div>
        </ThemeProvider>
    );
}

export default App;
